.survey-form-page {
  min-height: 100vh;
  background: #0a0a0a;
  padding: 20px;
}

.survey-form-container {
  max-width: 800px;
  margin: 0 auto;
  background: #1a1a1a;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  border: 1px solid #333;
}

.form-header {
  text-align: center;
  margin-bottom: 30px;
}

.form-header h1 {
  color: #e1e5e9;
  margin-bottom: 15px;
  font-size: 2rem;
  font-weight: 700;
}

.admin-indicator {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 15px;
  display: inline-block;
  box-shadow: 0 3px 10px rgba(102, 126, 234, 0.3);
}

.survey-type-display {
  color: #a0a0a0;
  font-size: 1.1rem;
  margin: 0;
}

.survey-type-display strong {
  color: #667eea;
  font-weight: 600;
}

.back-button {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 8px 16px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.back-button:hover {
  background: rgba(255, 255, 255, 0.3);
}

.survey-form {
  padding: 30px;
}

.form-section {
  margin-bottom: 25px;
}

.form-label {
  display: block;
  margin-bottom: 8px;
  color: #e1e5e9;
  font-weight: 600;
  font-size: 1rem;
}

.image-input {
  width: 100%;
  padding: 12px;
  border: 2px dashed #555;
  border-radius: 10px;
  background: #1f1f1f;
  cursor: pointer;
  transition: all 0.3s ease;
  color: #e1e5e9;
}

.image-input:hover {
  border-color: #667eea;
  background: #2a2a2a;
}

.image-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.image-preview {
  margin-top: 15px;
  text-align: center;
}

.image-preview img {
  max-width: 100%;
  max-height: 200px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.form-textarea {
  width: 100%;
  min-height: 100px;
  padding: 15px;
  border: 2px solid #333;
  border-radius: 10px;
  font-size: 1rem;
  font-family: inherit;
  resize: vertical;
  transition: all 0.3s ease;
  background: #1f1f1f;
  color: #e1e5e9;
}

.form-textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  background: #2a2a2a;
}

.form-textarea::placeholder {
  color: #888;
}

.error-message {
  background: #2d1b1b;
  color: #ff6b6b;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 20px;
  border: 1px solid #4a2a2a;
  text-align: center;
}

.submit-button {
  width: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 18px;
  border-radius: 12px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.submit-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.4);
}

.submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

/* Mobile responsive */
@media (max-width: 768px) {
  .survey-form-page {
    padding: 15px;
  }
  
  .form-header {
    padding: 20px;
    flex-direction: column;
    text-align: center;
    gap: 15px;
  }
  
  .form-header h1 {
    font-size: 1.5rem;
  }
  
  .survey-form {
    padding: 25px;
  }
  
  .form-section {
    margin-bottom: 20px;
  }
  
  .form-textarea {
    min-height: 80px;
    padding: 12px;
  }
  
  .submit-button {
    padding: 16px;
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .survey-form-page {
    padding: 10px;
  }
  
  .survey-form-container {
    border-radius: 15px;
  }
  
  .form-header {
    padding: 15px;
  }
  
  .form-header h1 {
    font-size: 1.3rem;
  }
  
  .survey-form {
    padding: 20px;
  }
  
  .form-label {
    font-size: 0.95rem;
  }
  
  .form-textarea {
    padding: 10px;
    font-size: 0.95rem;
  }
}
