.login-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  padding: 20px;
}

.login-container {
  background: #1a1a1a;
  border-radius: 20px;
  padding: 40px 30px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.5);
  text-align: center;
  max-width: 400px;
  width: 100%;
  border: 1px solid #333;
}

.login-title {
  color: #e1e5e9;
  font-size: 2rem;
  margin-bottom: 40px;
  font-weight: 600;
}

.user-options {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.user-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 18px 30px;
  border-radius: 15px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.3);
}

.user-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(102, 126, 234, 0.4);
}

.user-button:active {
  transform: translateY(-1px);
}

/* Mobile responsive */
@media (max-width: 480px) {
  .login-container {
    padding: 30px 20px;
    margin: 20px;
  }
  
  .login-title {
    font-size: 1.5rem;
    margin-bottom: 30px;
  }
  
  .user-button {
    padding: 16px 25px;
    font-size: 1.1rem;
  }
}
